/*--------------------------------------------------------------
# Only Roadmap
--------------------------------------------------------------*/

#roadmap {
  background: url("./media/roadmap-bg.png");
  background-color: #031e1f;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

#roadmap .per_row {
  list-style: none;
  padding: 0;
  margin-left: -10px;
  margin-right: -10px;
  display: flex;
  flex-wrap: wrap;
}

#roadmap .per_row > li {
  padding: 0px 10px;
  width: 33.33%;
  margin: 1.5rem 0;
  position: relative;
}

#roadmap .per_row > li:nth-child(1) {
  order: 1;
}

#roadmap .per_row > li:nth-child(2) {
  order: 2;
}

#roadmap .per_row > li:nth-child(3) {
  order: 3;
}

#roadmap .per_row > li:nth-child(4) {
  order: 6;
}

#roadmap .per_row > li:nth-child(5) {
  order: 5;
}

#roadmap .per_row > li:nth-child(6) {
  order: 4;
}

#roadmap .per_row > li:nth-child(7) {
  order: 7;
}

#roadmap .per_row > li:nth-child(8) {
  order: 8;
}

#roadmap .per_row > li:nth-child(9) {
  order: 9;
}

#roadmap .per_row > li:nth-child(10) {
  order: 12;
