  order: 11;
}

#roadmap .per_row > li:nth-child(12) {
  order: 10;
}

#roadmap .per_row > li:nth-child(13) {
  order: 13;
}

#roadmap .per_row > li:nth-child(14) {
  order: 14;
}
#roadmap .per_row > li:nth-child(15) {
  order: 15;
}
#roadmap .per_row > li:nth-child(16) {
  order: 16;
}

#roadmap .per_row > li::after {
  content: "";
  position: absolute;
  /* height: 4px; */
  width: 100%;
  top: 1.4rem;
  left: 50%;
  /* background: url('../media/dash.png'); */
  border-top: 2px dashed #4ffcb3;
  z-index: 1;
  display: none;
}

#roadmap .per_row > li.last::after {
  width: 200%;
}

#roadmap .per_row > li::before {
  content: "";
  position: absolute;
  /* width: 4px; */
  height: 100%;
  top: 3.8rem;
  left: 50%;
  /* background: url('../media/bar.png'); */
  border-left: 2px dashed #4ffcb3;
  z-index: 1;
  display: none;
}

#roadmap .per_row > li:nth-child(1)::after,
#roadmap .per_row > li:nth-child(2)::after,
#roadmap .per_row > li:nth-child(5)::after,
#roadmap .per_row > li:nth-child(6)::after,
#roadmap .per_row > li:nth-child(7)::after,
#roadmap .per_row > li:nth-child(8)::after,
#roadmap .per_row > li:nth-child(11)::after,
#roadmap .per_row > li:nth-child(12)::after,
#roadmap .per_row > li:nth-child(13)::after,
#roadmap .per_row > li:nth-child(14)::after,
#roadmap .per_row > li:nth-child(16)::after {
  display: block;
}

#roadmap .per_row > li:nth-child(3)::before,
#roadmap .per_row > li:nth-child(6)::before,
#roadmap .per_row > li:nth-child(6)::before,
