#roadmap .per_row > li:nth-child(12)::before,
#roadmap .per_row > li:nth-child(15)::before {
  display: block;
}

#roadmap .per_row > li .per {
  position: relative;
  width: 4rem;
  height: 4rem;
  text-align: center;
  line-height: 4rem;
  background: #2adeea;
  border-radius: 100%;
  margin: 0 auto;
  margin-bottom: -2rem;
  z-index: 3;
  color: #000000;
  font-size: 1.5rem;
  font-weight: 700;
}

#roadmap .per_row > li .per small {
  font-size: 1rem;
  font-weight: 500;
}

#roadmap .per_row > li .box {
  background: #21353b;
  height: 12rem;
  padding: 0px 1rem;
  text-align: center;
  position: relative;
  border-radius: 35px;
  z-index: 2;
  box-shadow: 0px 0px 15px rgba(5, 165, 207, 0.6);
}

#roadmap .per_row > li.on .box,
#roadmap .per_row > li.finish .box {
  box-shadow: none;
}

#roadmap .per_row > li .box > div {
  position: absolute;
  left: 0;
  width: 100%;
  top: 50%;
  transform: translatey(-50%);
  font-size: 18px;
  /* font-weight: 300; */
  color: #fff;
  padding: 0px 25px;
  z-index: 5;
}

#roadmap .per_row > li .box > div span {
  color: #2adeea;
  /* font-weight: 500; */
}

#roadmap .per_row > li .box > div p {
  padding-left: 12px;
  /* font-size: 1.6rem; */
  text-align: left;
  position: relative;
}

#roadmap .per_row > li .box > div p + p {
  margin-top: 1.5rem;
}

#roadmap .per_row > li .box > div p::after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  width: 4px;
  height: 4px;
  background: #2adeea;
  border-radius: 100%;
}

#roadmap .per_row > li.on .per {
  color: #4ffcb3;
  background: #21353b;
  box-shadow: 0px 0px 15px rgba(5, 165, 207, 0.6);
}

#roadmap .per_row > li.on .per span {
  position: relative;
  z-index: 10;
}

#roadmap .per_row > li.on .per::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
  border-radius: 100%;
  background-origin: border-box;
  background-clip: content-box, border-box;
}

#roadmap .per_row > li .box::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
  border-radius: 35px;
  background-image: linear-gradient(#21353b, #21353b),
    linear-gradient(to top, #0684e5, #03f29d);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

#roadmap .per_row > li.on .box::after,
#roadmap .per_row > li.finish .box::after {
  display: none;
}

#roadmap .per_row > li.finish .box {
  background: rgb(29, 76, 70);
  background: -moz-linear-gradient(
    120deg,
    rgba(29, 76, 70, 1) 30%,
    rgba(30, 54, 84, 1) 100%
  );
  background: -webkit-linear-gradient(
    120deg,
    rgba(29, 76, 70, 1) 30%,
    rgba(30, 54, 84, 1) 100%
  );
  background: linear-gradient(
    120deg,
    rgba(29, 76, 70, 1) 30%,
    rgba(30, 54, 84, 1) 100%
  );
  filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#1d4c46", endColorstr="#1e3654", GradientType=1);
}

#roadmap .per_row > li.finish .box > div {
  color: #02f29d;
  font-weight: 600;
  font-size: 2rem;
}

#roadmap .coin_item_pop {
  position: absolute;
  animation: bubblemove 2.5s infinite linear;
}

#roadmap .coin_item_pop.item1 {
  right: 0rem;
  top: 0rem;
}

#roadmap .coin_item_pop.item2 {
