  text-align: center;
  margin-top: 5rem;
}

#roadmap .detail_roadmap a {
  display: inline-block;
  border-radius: 10px;
  background: rgb(2, 121, 79);
  background: -moz-linear-gradient(
    120deg,
    rgba(2, 121, 79, 1) 0%,
    rgba(3, 66, 115, 1) 100%
  );
  background: -webkit-linear-gradient(
    120deg,
